import React, { Component } from "react";
import "react-calendar/dist/Calendar.css";
import "react-datepicker/dist/react-datepicker.css";
import { Grid, Row, Col, Button } from "react-bootstrap";
import ReactTooltip from 'react-tooltip';
var moment = require("moment");

class FilterDetail extends Component {
  constructor(props) {
    super(props);
    this.state = {
      autoSyncClose: false,
      exclude: false,
      include: false,
      excludeTo: false,
      includeTo: false,
      excludeTicket: false,
      includeTicket: false,
      excludeTicketTo: false,
      includeTicketTo: false,
      showMagicNumberFrom: false,
      showMagicNumberTo: false,
      showTicketFrom: false,
      showTicketTo: false,
      includeComm: false,
      excludeComm: false,
      showMagicNumberComm: false,
    };
  }
  crossClick(e) {
    e.preventDefault();
    this.props.onHide();
  }

  componentDidMount() {
    let data = this.props.data;
    this.setState({
      autoSyncClose: data.auto_close == 1 ? true : false,
      include: data.from_include_exclude_status == 1 ? true : false,
      exclude: data.from_include_exclude_status == 2 ? true : false,
      includeTo: data.to_include_exclude_status == 1 ? true : false,
      excludeTo: data.to_include_exclude_status == 2 ? true : false,
      includeComm: data.comm_include_exclude_status == 1 ? true : false,
      excludeComm: data.comm_include_exclude_status == 2 ? true : false,
      showMagicNumberComm: data.comm_include_exclude_status !== 0 ? true : false,
      includeTicket:
        data.from_include_exclude_status_ticket == 1 ? true : false,
      excludeTicket:
        data.from_include_exclude_status_ticket == 2 ? true : false,
      includeTicketTo:
        data.to_include_exclude_status_ticket == 1 ? true : false,
      excludeTicketTo:
        data.to_include_exclude_status_ticket == 2 ? true : false,
      showMagicNumberFrom:
        data.from_include_exclude_status !== 0 ? true : false,
      showMagicNumberTo: data.to_include_exclude_status !== 0 ? true : false,
      showTicketFrom:
        data.from_include_exclude_status_ticket !== 0 ? true : false,
      showTicketTo: data.to_include_exclude_status_ticket !== 0 ? true : false,
    });
  }

  // componentWillMount = () => {
  //     console.log(this.props,'props..................');
  // }

  render() {
    let data = this.props.data;
    console.log(data);
    let frm_ticket = data.from_ticket !== "" && JSON.parse(data.from_ticket);
    let to_ticket = data.to_ticket !== "" && JSON.parse(data.to_ticket);
    let frm_magic =
      data.from_magic_number !== "" && JSON.parse(data.from_magic_number);
    let to_magic =
      data.to_magic_number !== "" && JSON.parse(data.to_magic_number);
    let comm_magic_number = data.comm_magic_number !== "" && JSON.parse(data.comm_magic_number);
    let from_symbol = JSON.parse(data.from_symbols);
    let to_symbol = JSON.parse(data.to_symbols);
    from_symbol = from_symbol.join(", ");
    to_symbol = to_symbol.join(", ");
    return (
      <div className="content">
        <Grid fluid>
          <div className="symbol-box">
            <div className="cross_div">
              <a onClick={(e) => this.crossClick(e)}>
                <i className="fa fa-times" aria-hidden="true"></i>
              </a>
            </div>
            <div className="detail-text">
              <h3> {data.profile_name} </h3>
            </div>
            <div className="synclose">
              <label className="checkbox-inline">
                <input
                  type="checkbox"
                  checked={this.state.autoSyncClose}
                  name="changeAutoSync"
                />
                <b>Auto Sync Close</b>
              </label>
            </div>
            <Row>
              <Col md={6}>
                <div className="card">
                  <div
                    className="detail_account"
                    style={{ padding: "10px 20px 0px" }}
                  >
                    <span style={{ fontWeight: "bold" }}>Account A</span>
                  </div>
                  <table className="table mt20">
                    <tbody>
                      <tr>
                        <td className="b-clr">
                          <span>Account :</span>
                        </td>

                        <td
                          className="b-clr text-center"
                          style={{ width: "50%" }}
                        >
                          {data.accountFromInfo[0].login}
                        </td>
                      </tr>
                      <tr>
                        <td className="b-clr ">
                          <span>From : </span>
                        </td>
                        <td className="b-clr text-center datepicker_filter">
                          {moment(data.startdateFrom).format("DD/MM/YYYY")}
                        </td>
                      </tr>
                      <tr>
                        <td className="b-clr ">
                          <span>To : </span>
                        </td>
                        <td className="b-clr text-center datepicker_filter">
                          {data.enddateFrom !== null
                            ? moment(data.enddateFrom).format("DD/MM/YYYY")
                            : "-"}
                        </td>
                      </tr>
                      <tr>
                        <td className="b-clr ">
                          <span>Symbol : </span>
                        </td>
                        <td
                          className="b-clr text-center"
                          style={{ width: "50%" }}
                        >
                          {from_symbol}
                        </td>
                      </tr>
                      {this.state.showMagicNumberFrom && (
                        <>
                          <tr>
                            <td className="b-clr ">
                              <span>Magic Number : </span>
                            </td>

                            <td
                              className="b-clr text-center"
                              style={{ width: "50%" }}
                            >
                              <div
                                className="row"
                                style={{
                                  width: "100%",
                                  display: "flex",
                                  flexWrap: "wrap",
                                }}
                              >
                                {frm_magic &&
                                  frm_magic.map((item, index) => (
                                    <div key={index} className="tags-all">
                                      {item}
                                    </div>
                                  ))}
                              </div>{" "}
                            </td>
                          </tr>
                          <tr>
                            <td className="b-clr dataAdjust">
                              <div onClick={this.changeIncludeDefaultProperty}>
                                <input
                                  type="radio"
                                  id="include"
                                  name="include"
                                  checked={this.state.include}
                                />
                                <span className="accnt ">Include</span>
                              </div>
                            </td>
                            <td className="b-clr ">
                              <div onClick={this.changeExcludeDefaultProperty}>
                                <input
                                  type="radio"
                                  id="exclude"
                                  name="exclude"
                                  checked={this.state.exclude}
                                />
                                <span className="accnt ">Exclude</span>
                              </div>
                            </td>
                          </tr>
                        </>
                      )}
                      {this.state.showTicketFrom && (
                        <>
                          <tr>
                            <td className="b-clr ">
                              <span>Ticket : </span>
                            </td>

                            <td
                              className="b-clr text-center"
                              style={{ width: "50%" }}
                            >
                              <div
                                className="row"
                                style={{
                                  width: "100%",
                                  display: "flex",
                                  flexWrap: "wrap",
                                }}
                              >
                                {frm_ticket &&
                                  frm_ticket.map((item, index) => (
                                    <div key={index} className="tags-all">
                                      {item}
                                    </div>
                                  ))}
                              </div>{" "}
                            </td>
                          </tr>
                          <tr>
                            <td className="b-clr dataAdjust">
                              <div
                                onClick={
                                  this.changeIncludeDefaultPropertyTicket
                                }
                              >
                                <input
                                  type="radio"
                                  id="includeTicket"
                                  name="includeTicket"
                                  checked={this.state.includeTicket}
                                />
                                <span className="accnt ">Include</span>
                              </div>
                            </td>
                            <td className="b-clr ">
                              <div
                                onClick={
                                  this.changeExcludeDefaultPropertyTicket
                                }
                              >
                                <input
                                  type="radio"
                                  id="excludeTicket"
                                  name="excludeTicket"
                                  checked={this.state.excludeTicket}
                                />
                                <span className="accnt ">Exclude</span>
                              </div>
                            </td>
                          </tr>
                        </>
                      )}
                    </tbody>
                  </table>
                </div>
              </Col>
              <Col md={6}>
                <div className="card">
                  <div
                    className="detail_account"
                    style={{ padding: "10px 20px 0px" }}
                  >
                    <span style={{ fontWeight: "bold" }}>Account B</span>
                  </div>

                  <table className="table mt20">
                    <tbody>
                      <tr>
                        <td className="b-clr ">
                          <span>Account :</span>
                        </td>
                        <td
                          className="b-clr text-center"
                          style={{ width: "50%" }}
                        >
                          {data.accountToInfo[0].login}
                        </td>
                      </tr>
                      <tr>
                        <td className="b-clr ">
                          <span>From : </span>
                        </td>
                        <td className="b-clr text-center datepicker_filter">
                          {moment(data.startdateTo).format("DD/MM/YYYY")}
                        </td>
                      </tr>
                      <tr>
                        <td className="b-clr ">
                          <span>To : </span>
                        </td>
                        <td className="b-clr text-center datepicker_filter">
                          {data.enddateTo != null
                            ? moment(data.enddateTo).format("DD/MM/YYYY")
                            : "-"}
                        </td>
                      </tr>
                      <tr>
                        <td className="b-clr">
                          <span>Symbol : </span>
                        </td>
                        <td
                          className="b-clr text-center"
                          style={{ width: "50%" }}
                        >
                          {to_symbol}
                        </td>
                      </tr>
                      {this.state.showMagicNumberTo && (
                        <>
                          <tr>
                            <td className="b-clr">
                              <span>Magic Number : </span>
                            </td>
                            <td
                              className="b-clr text-center"
                              style={{ width: "50%" }}
                            >
                              <div
                                className="row"
                                style={{
                                  width: "100%",
                                  display: "flex",
                                  flexWrap: "wrap",
                                }}
                              >
                                {to_magic &&
                                  to_magic.map((item, index) => (
                                    <div key={index} className="tags-all">
                                      {item}
                                    </div>
                                  ))}
                              </div>{" "}
                            </td>
                          </tr>

                          <tr>
                            <td className="b-clr checkboxAdjust">
                              <div
                                onClick={this.changeIncludeDefaultPropertyTo}
                              >
                                <input
                                  type="radio"
                                  id="includeTo"
                                  name="includeTO"
                                  checked={this.state.includeTo}
                                />
                                <span className="accnt ">Include</span>
                              </div>
                            </td>
                            <td className="b-clr checkboxAdjust">
                              <div
                                onClick={this.changeExcludeDefaultPropertyTo}
                              >
                                <input
                                  type="radio"
                                  id="exclude"
                                  name="exclude"
                                  checked={this.state.excludeTo}
                                />
                                <span className="accnt ">Exclude</span>
                              </div>
                            </td>
                          </tr>
                        </>
                      )}
                      {this.state.showTicketTo && (
                        <>
                          <tr>
                            <td className="b-clr">
                              <span>Ticket : </span>
                            </td>
                            <td
                              className="b-clr text-center"
                              style={{ width: "50%" }}
                            >
                              <div
                                className="row"
                                style={{
                                  width: "100%",
                                  display: "flex",
                                  flexWrap: "wrap",
                                }}
                              >
                                {to_ticket &&
                                  to_ticket.map((item, index) => (
                                    <div key={index} className="tags-all">
                                      {item}
                                    </div>
                                  ))}
                              </div>
                            </td>
                          </tr>

                          <tr>
                            <td className="b-clr checkboxAdjust">
                              <div
                                onClick={
                                  this.changeIncludeDefaultPropertyTicketTo
                                }
                              >
                                <input
                                  type="radio"
                                  id="includeTicketTo"
                                  name="includeTicketTo"
                                  checked={this.state.includeTicketTo}
                                />
                                <span className="accnt ">Include</span>
                              </div>
                            </td>
                            <td className="b-clr checkboxAdjust">
                              <div
                                onClick={
                                  this.changeExcludeDefaultPropertyTicketTo
                                }
                              >
                                <input
                                  type="radio"
                                  id="excludeTicketTo"
                                  name="excludeTicketTo"
                                  checked={this.state.excludeTicketTo}
                                />
                                <span className="accnt ">Exclude</span>
                              </div>
                            </td>
                          </tr>
                        </>
                      )}
                    </tbody>
                  </table>
                </div>
              </Col>
              {data.commission_acount_id !== 0 && (
                <Col md={12}>
                  <div className="card bottom">
                    <div
                      className="detail_account"
                      style={{ padding: "10px 20px 0px" }}
                    >
                      <h3 style={{ fontWeight: "bold" }} data-tip="Commission">Commission</h3>
                      <ReactTooltip />
                    </div>
                    <table className="table mt20">
                      <tbody>
                        <tr>
                          <td
                            className="b-clr text-center"
                            style={{ width: "50%" }}
                          >
                            <span>Account :</span>
                          </td>
                          <td
                            className="b-clr text-center"
                            style={{ width: "50%" }}
                          >
                            {data.accountCommissionInfo[0].login}
                          </td>
                        </tr>
                        <tr>
                          <td className="b-clr text-center" style={{ width: "50%" }}>
                            <span>From : </span>
                          </td>
                          <td className="b-clr text-center datepicker_filter" style={{ width: "50%" }}>
                            {moment(data.startdateComm).format("DD/MM/YYYY")}
                          </td>
                        </tr>
                        <tr>
                          <td className="b-clr text-center" style={{ width: "50%" }}>
                            <span>To : </span>
                          </td>
                          <td className="b-clr text-center datepicker_filter" style={{ width: "50%" }}>
                            {data.enddateComm != null
                              ? moment(data.enddateComm).format("DD/MM/YYYY")
                              : "-"}
                          </td>
                        </tr>
                        {this.state.showMagicNumberComm && (
                          <>
                            <tr>
                              <td className="b-clr text-center" style={{ width: "50%" }}>
                                <span>Magic Number : </span>
                              </td>
                              <td
                                className="b-clr text-center"
                                style={{ width: "50%" }}
                              >
                                <div
                                  className="row"
                                  style={{
                                    width: "100%",
                                    display: "flex",
                                    flexWrap: "wrap",
                                    justifyContent: "center"
                                  }}
                                >
                                  {comm_magic_number &&
                                    comm_magic_number.map((item, index) => (
                                      <div key={index} className="tags-all">
                                        {item}
                                      </div>
                                    ))}
                                </div>{" "}
                              </td>
                            </tr>
                            <tr>
                              <td className="b-clr text-center dataAdjust">
                                <div onClick={this.changeIncludeDefaultProperty}>
                                  <input
                                    type="radio"
                                    id="includeComm"
                                    name="includeComm"
                                    checked={this.state.includeComm}
                                  />
                                  <span className="accnt ">Include</span>
                                </div>
                              </td>
                              <td className="b-clr text-center">
                                <div onClick={this.changeExcludeDefaultProperty}>
                                  <input
                                    type="radio"
                                    id="excludeComm"
                                    name="excludeComm"
                                    checked={this.state.excludeComm}
                                  />
                                  <span className="accnt ">Exclude</span>
                                </div>
                              </td>
                            </tr>
                          </>
                        )}
                      </tbody>
                    </table>
                  </div>
                </Col>
              )}
            </Row>
          </div>
        </Grid>
      </div>
    );
  }
}

export default FilterDetail;
